on.scss';\n@import '../style/blocks/title.scss';\n@import '../style/blocks/header.scss';\n@import '../style/blocks/nav.scss';\n@import '../style/blocks/main-section.scss';\n@import '../style/blocks/about-us.scss';\n@import '../style/blocks/our-works.scss';\n@import '../style/blocks/team.scss';\n@import '../style/blocks/reviews.scss';\n@import '../style/blocks/form.scss';\n@import '../style/blocks/request.scss';\n@import '../style/blocks/map.scss';\n@import '../style/blocks/footer.scss';\n\n","body {\n\tbackground-color: $bg;\n  min-width: 320px;\n  margin: 0;\n  padding: 0;\n  font-family: $font;\n  font-size: 16px;\n  font-weight: 400;\n  color: $text;\n}\n\nimg {\n  display: block;\n  min-height: auto;\n  width: 100%;\n}\n\n*,\n*::after,\n*::before {\n  box-sizing: border-box;\n}\n","$text: #533738;\n$white: #FFFFFF;\n$input-text: #F6EDE9;\n\n\n$bg: #EFE6E4;\n$bg-team: #E2C4BA;\n$bg-button: #CC8B86;\n$bg-about-us: #8D7370;\n$bg-form: #8D7370;\n\n$bg-number: #8D7370;\n\n$font: 'Montserrat', \"Arial\", sans-serif;\n$desktop: 1024px;\n",".container {\n  margin: 0 auto;\n  min-width: 320px;\n  padding: 0 30px;\n\n  @media (min-width: $desktop) {\n    max-width: 1400px;\n  }\n\n}\n",".button {\n  width: 100%;\n  min-height: 74px;\n  border: none;\n  background-color: $bg-button;\n  color: $text;\n  cursor: pointer;\n\n  @media (min-width: $desktop) {\n    width: 309px;\n  }\n}\n",".title {\n  font-size: 50px;\n  line-height: 62px;\n  font-weight: 400;\n  margin: 0;\n}\n",".header {\n  background-image: url(/src/img/vysotskaya.jpg);\n  background-repeat: no-repeat;\n  background-position: top;\n  background-size: cover;\n  min-height: 100%;\n}\n\n.header__wrapper {\n  padding: 38px 30px 138px 30px;\n\n  @media (min-width: $desktop) {\n    min-height: 29px;\n    display: flex;\n    align-items: center;\n    margin-right: auto;\n    padding: 38px 100px 0 134px;\n  }\n\n}\n\n.header__logo {\n  padding-top: 30px;\n  margin-bottom: 30px;\n\n  @media (min-width: $desktop) {\n    margin-bottom: 0;\n    padding-top: 0;\n  }\n\n  a {\n    align-items: center;\n    display: flex;\n    flex-shrink: 0;\n    text-decoration: none;\n    color: $text;\n    width: 90px;\n  }\n\n  .header__logo-text {\n    margin: 0;\n  }\n}\n",".nav {\n\n  @media (min-width: $desktop) {\n    margin-left: 110px;\n  }\n}\n\n.nav__list {\n  @include clear-list;\n  justify-content: center;\n  width: 175px;\n  display: flex;\n  flex-wrap: wrap;\n\n  @media (min-width: $desktop) {\n    column-gap: 28px;\n    row-gap: 28px;\n    text-align: start;\n    width: 100%;;\n  }\n\n  .nav__list-item {\n    padding: 15px;\n    flex: 1 auto;\n\n    @media (min-width: $desktop) {\n      padding: 0;\n    }\n  }\n\n  .nav__list-item:nth-last-child(1) {\n    margin-left: 0;\n\n    @media (min-width: $desktop) {\n      margin-left: 10px;\n    }\n  }\n\n  .nav__list-link {\n    text-decoration: none;\n    color: $text;\n    line-height: 19px;\n  }\n}\n","\n//clear-list\n@mixin clear-list {\n  list-style-type: none;\n  margin: 0;\n  padding: 0;\n}\n\n//Blocks\n@mixin desktop {\n  max-width: 1400px;\n  padding: 0 90px 0;\n}\n",".main-section {\n  margin-bottom: 65px;\n  background-color: rgba(200, 190, 218, 0.3);\n\n  @media (min-width: $desktop) {\n    background-color: transparent;\n  }\n\n  .main-section__wrapper {\n    padding-top: 150px;\n    padding-bottom: 50px;\n\n    @media (min-width: $desktop) {\n      padding-left: 184px;\n      padding-bottom: 168px;\n    }\n  }\n}\n\n.main-section__title {\n  margin-bottom: 24px;\n  text-align: center;\n  @media (min-width: $desktop) {\n    text-align: start;\n    width: 519px;\n  }\n}\n\n.main-section__text {\n  display: none;\n  margin: 0;\n  font-size: 18px;\n  line-height: 30px;\n  margin-bottom: 21px;\n  text-align: center;\n\n  @media (min-width: $desktop) {\n    display: block;\n    text-align: start;\n    width: 363px;\n  }\n}\n\n.main-section__button {\n  font-size: 20px;\n  line-height: 24px;\n  font-weight: 600;\n}\n",".about-us 